body{
background-color:grey;
  display: flex;
  align-items: center;
  justify-content:center;
  padding-top: 30px;
  min-height: 100vh;
}


.title{

font-size: 20px;
width: 400px;
text-align: center;
margin: 15px;
color:black;
font-weight: 900;
font-family: fantasy;
}


.container{

display: flex;
align-items: center;
justify-content: center;
background-color: black;
width: 400px;
height: 300px;
border-radius: 10px;
padding: 0 10px 0 10px;
box-shadow: inset 1px  1px 20px   white;
opacity: 80%;
}



button{

height: 50px;
width: 120px;
font-size: 18px;
margin: 5px;
border-radius: 15px;
box-shadow: 2px  4px white;
cursor: pointer;

}

button:hover{
  color: greenyellow;
}
button:active{

 transform: rotateY(45deg);
 transition: .6ms;
}



.button-one{
  background-color: green;

  font-size: 40px;
}


.button-two{
background-color: rgb(81, 81, 233);
}

.button-three{
  background-color: red;
  font-size: 40px;
  }


.box{

  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 120px;
  background-color: #fff;
  text-align: center;
  margin: 5px;
  font-size: 35px;
border-radius: 15px;
background-color: transparent;
color: white;
border: 1px solid white;
text-shadow: 1px 0 3px;
}

.box::after{

transform: rotateX(180deg);

}
